@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .control-div {
    @apply footer-container grid-container;
  }
  .control-btn {
    @apply bg-blue-300 border-blue-700 text-blue-900 rounded-md border-2 shadow-lg p-2
    hover:bg-blue-400 hover:text-white hover:border-blue-900 hover:shadow-xl;
  }
  .answer-div {
    @apply grid-container grid-cols-2 grid-rows-2 gap-x-10 gap-y-4 p-16;
  }
  .answer-btn {
    @apply bg-green-300 border-green-700 text-green-900 rounded-xl border-2 shadow-xl p-8
    hover:bg-green-400 hover:text-white hover:border-green-900 hover:shadow-2xl;
  }
}

@layer utilities {
  .grid-container {
    display:grid;
  }
  .footer-container {
    position:fixed;
    left:0px;
    bottom:0px;
    height:30px;
    width:100%;
  }
}